* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body {
    margin: 30px 20px ;
}


* {
    box-sizing: border-box;
    margin:0;
    padding: 0;
}

body {
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.4;
    background: #EFE1BA;
    
}

.app__container {
   padding: 30px 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

@media (max-width: 900px ) {
    .app__container {
         display: -webkit-flex;
         display: flex;
         -webkit-flex-direction: column;
                 flex-direction: column;
         -webkit-align-items: center;
                 align-items: center;
         padding: 20px 0px;
        
     }
}

.app__heading {
    text-align: center;
    font-size: 35px;
    font-weight: 400;
    text-transform: capitalize;
    margin: 20px 0 40px;
    color: #123D6A;
    text-decoration: underline;
}


.app__form {
    height: 315px;
    width: 350px;
    background: #D79922;
    padding: 20px 0 30px;
    border-radius: 5px;
    
}

@media (max-width: 900px ) {
    .app__form {
        height: 315px;
        width: 80%;    
    }
}

.app__form input,
.app__form input:focus,
.app__form textarea {
    display: block;
    width: 90%;
    margin: 20px auto;
    padding: 7px 8px;
    border: none;
    border-radius: 5px;
    outline:none;
}

.app__form input::-webkit-input-placeholder,
.app__form textarea::-webkit-input-placeholder {
    font-weight: bold;
    color: #999;
}

.app__form input::-ms-input-placeholder,
.app__form textarea::-ms-input-placeholder {
    font-weight: bold;
    color: #999;
}

.app__form input::placeholder,
.app__form textarea::placeholder {
    font-weight: bold;
    color: #999;
}

.app__form__heading {
    margin: 0 0 30px;
    color:#4056A1;
    text-align: center;
    font-weight: 400;
    text-transform: uppercase;
}

.app__form button {
    display: block;
    width: 40%;
    margin: 15px auto;
    background: #4056A1;
    color: #EFE1BA;
    padding: 6px;
    font-size: 17px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    border: none;
    border-radius: 100px;
    transition: all .4s ease;
    outline: none;
}

.app__form button:hover {
    background: #EFE1BA;
    color: #4056A1;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}
.todos__container {
    width: 60%;
}

@media (max-width: 900px ) {
    .todos__container {
        margin-top: 30px;
        width: 85%;
    }
}

.todo {
    width: 90%;
    margin: 15px auto;
    padding: 14px 25px;
    background: #4056A1;
    color: #EFE1BA;
    border-radius: 4px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.todo__describtion p {
    font-size: 14px;
    margin: 5px 0;

}

.todo__remove i {
    color: #F13C1F;
    font-size: 20px;
    cursor: pointer;
    transition: all .4s ease;

}

.todo__remove i:hover {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}

.noTodo {
    text-align: center;
    color: #4155A1;
    font-weight: 400;
    margin-top: 40px;
}
